<!-- 雷达图组件 by yankui.cao -->
<template>
  <canvas ref="canvas" style="width: 320px;height: 320px;"></canvas>
</template>

<script>
export default {
  data() {
    return {
      canvas: {
        center: 160,
        radius: 85,
        angle: Math.PI / 3,
        count: 6,
        ratio: 1,
        axis: 3,
        fixRadius: -Math.PI / 2
      },
      timer: null
    };
  },
  props: {
    list: Array
  },
  methods: {
    _initRadar() {
      let canvas = this.$refs.canvas;
      let ctx = canvas.getContext("2d");
      let ratio = this.getPixelRatio(ctx);
      canvas.height = 320 * ratio;
      canvas.width = 320 * ratio;
      this.canvas.radius = 85 * ratio;
      this.canvas.center = 160 * ratio;
      this.canvas.ratio = ratio;
      this.drawPolygon(ctx); //极坐标网
      this.drawLines(ctx); //极坐标轴
      this.drawText(ctx); //文本内容
      let scale = 0.1;
      this.timer = setInterval(() => {
        scale += 0.1;
        this.drawRegion(ctx, scale);
        if (scale > 1) {
          clearInterval(this.timer);
          return;
        }
      }, 100);
    },
    drawPolygon(ctx) {
      let {
        ratio,
        count,
        radius,
        center,
        angle,
        axis,
        fixRadius
      } = this.canvas;
      ctx.save();
      ctx.strokeStyle = "#EAEAEA"; //多边形颜色
      ctx.lineWidth = 2 * ratio;
      let r = radius / axis; //单位半径
      let curr, x, y, i, j, angles;
      for (i = 0; i < axis; i++) {
        ctx.beginPath();
        curr = r * (i + 1); //当前半径
        for (j = 0; j < count; j++) {
          angles = angle * j;
          x = center + curr * Math.cos(angles + fixRadius);
          y = center + curr * Math.sin(angles + fixRadius);
          ctx.lineTo(x, y);
        }
        ctx.closePath();
        ctx.stroke();
      }
      ctx.restore();
    },
    drawLines(ctx) {
      let { ratio, count, radius, center, angle, fixRadius } = this.canvas;
      ctx.save();
      ctx.lineWidth = 2 * ratio;
      ctx.beginPath();
      ctx.strokeStyle = "#EAEAEA";
      let x, y, i, angles;
      for (i = 0; i < count; i++) {
        angles = angle * i;
        x = center + radius * Math.cos(angles + fixRadius);
        y = center + radius * Math.sin(angles + fixRadius);
        ctx.moveTo(center, center);
        ctx.lineTo(x, y);
      }
      ctx.stroke();
      ctx.restore();
    },
    drawText(ctx) {
      let { count, radius, center, angle, fixRadius } = this.canvas;
      ctx.save();
      let fontSize = center / 12;
      ctx.font = fontSize + "px Microsoft Yahei";
      ctx.fillStyle = "#40453F";
      let x, y, text, width, i, angles;
      for (i = 0; i < count; i++) {
        angles = angle * i + fixRadius;
        x = center + radius * Math.cos(angles);
        y = center + radius * Math.sin(angles);
        text = this.list[i][0];
        width = ctx.measureText(text).width;
        if (angles >= -Math.PI / 2 && angles < -Math.PI / 5) {
          ctx.fillText(text, x - width / 2, y - fontSize);
        } else if (angles > -Math.PI / 5 && angles < Math.PI / 2) {
          ctx.fillText(text, x + fontSize, y + fontSize / 2);
        } else if (angles >= Math.PI / 2 && angles < Math.PI * 2 / 3) {
          ctx.fillText(text, x - width / 2, y + fontSize * 2);
        } else {
          ctx.fillText(text, x - width - fontSize, y + fontSize / 2);
        }
      }
      ctx.restore();
    },
    drawRegion(ctx, scale) {
      let { count, radius, center, angle, ratio, fixRadius } = this.canvas;
      ctx.save();
      ctx.beginPath();
      let width, x, y, i, angles;
      for (i = 0; i < count; i++) {
        width = this.list[i][1]+10;
        angles = angle * i + fixRadius;
        x = center + scale * radius * Math.cos(angles) * width / 70;
        y = center + scale * radius * Math.sin(angles) * width / 70;
        ctx.lineTo(x, y);
      }
      ctx.closePath();
      if (scale > 1) {
        ctx.fillStyle = "rgba(196,234,208,0.6)";
        ctx.strokeStyle = "#2FB157";
      } else {
        ctx.fillStyle = "rgba(196,234,208,0.1)";
        ctx.strokeStyle = "rgba(196,234,208,0.1)";
      }
      ctx.lineWidth = 2 * ratio;
      ctx.fill();
      ctx.stroke();
      ctx.restore();
    },
    getPixelRatio(context) {
      let backingStore =
        context.backingStorePixelRatio ||
        context.webkitBackingStorePixelRatio ||
        context.mozBackingStorePixelRatio ||
        context.msBackingStorePixelRatio ||
        context.oBackingStorePixelRatio ||
        context.backingStorePixelRatio ||
        1;
      return (window.devicePixelRatio || 1) / backingStore;
    }
  },
  mounted() {
    this._initRadar();
  },
  destroyed(){
    clearInterval(this.timer);
  }
};
</script>