<template>
  <div class="pageSwipe" ref="pageSwipe">
    <slot></slot>
  </div>
</template>


<script>
import AlloyFinger from "alloyfinger";
export default {
  data() {
    return {
      currentIndex: 0,
      childNum: 1,
      duration: "0.2s",
      timing: "cubic-bezier(0.42, 0, 0.58, 1)",
      timer: null
    };
  },
  props: {
    bill: Object
  },
  methods: {
    _addStyle() {
      let ele = this.$refs.pageSwipe;
      let childs = ele.children;
      let height = 0;
      this.childNum = childs.length;
      Array.from(childs).forEach(child => {
        height += child.offsetHeight;
        if (child.style.transition)
          child.style.transition = `transform ${this.duration} ${this.timing};`;
        else
          child.style.WebkitTransition = `-webkit-transform ${this.duration} ${
            this.timing
          };`;
      });
      ele.style.height = height + "px";
    },
    _initSwipe() {
      let ele = this.$refs.pageSwipe;
      let me = this;
      this.swipe = new AlloyFinger(ele, {
        swipe: function(evt) {
          let offsetIndex = 0;
          if (me.bill.max_pay_money === "0.00") offsetIndex = 1;
          if (evt.direction == "Up") {
            me.currentIndex = Math.min(me.childNum - 1, ++me.currentIndex);
            if (me.currentIndex === 6) me.currentIndex += offsetIndex;
          }
          if (evt.direction == "Down") {
            me.currentIndex = Math.max(0, --me.currentIndex);
            if (me.currentIndex === 6) me.currentIndex -= offsetIndex;
          }
          me.$emit("onChange", me.currentIndex);
          me.timer = setTimeout(() => {
            me.goToSection(me.currentIndex);
            clearTimeout(me.timer);
          }, 500);
        }
      });
    },
    goToSection(targetIndex) {
      if(targetIndex >8){ this.$emit("onEnd"); return}
      try {
        let childs = this.$refs.pageSwipe.children;
        Array.from(childs).forEach(child => {
          if (child.style.transform)
            child.style.transform = `translateY(-${100 * targetIndex}%)`;
          else
            child.style.WebkitTransform = `translateY(-${100 * targetIndex}%)`;
        });
      } catch (error) {
        return;
      }
    },
    _preventDefault(e){
      e.preventDefault();
    }
  },

  mounted() {
    document.body.addEventListener("touchmove",this._preventDefault,{ passive: false });
    this._addStyle();
    this._initSwipe();
  },
  destroyed() {
    document.body.removeEventListener('touchmove',this._preventDefault);
  }, 
};
</script>

<style lang="scss" scoped>
.pageSwipe {
  height: 100% !important;
}
</style>


