<script>
import share from '@/mixins/share'
import PageSwipe from "@/components/bill/PageSwipe";
import RadarCanvas from "@/components/bill/RadarCanvas";
import API from "@/server/api";
export default {
  name: "bill",
  components: { PageSwipe, RadarCanvas },
  mixins: [share],
  data() {
    return {
      pageIndex: -1,
      mydata: null,
      minute: 0,
      day: 0,
      showCode: false
    };
  },
  async created() {
    let {friend_id=''} = this.$route.query;
    this.$http.get(API.userBill('detailInfo'),{friend_id}, true, false).then((res)=>{
      if(res.error) {
        this.goLink('/bill');
        return ;
      }
      this.mydata = res.data;
      let {is_new_user,total_order_cnt} = this.mydata;
      if(is_new_user) {
          this.goLink('/bill/pack');
          return ;
      }
      let minutes = (total_order_cnt %20+40)*total_order_cnt;
      let day = minutes/480;
      let div = minutes%480;
      this.minute = minutes;
      if(div&&day.toString().split(".")[1].length) day =day.toFixed(1);
      if(day==="0.0") day = 0;
      this.day=day;
    });
    this.pageIndex = 0;
    this.showCode = true;
  },
  methods: {
    onSwiper(index) {
      index > this.pageIndex && this.log({
          cid:'mybill',
          aid:'next'
      });
      this.$set(this.$data, "pageIndex", index);
    },
    goLink(path){
      this.$router.push({path})
    },
    openBill(){
      this.log({
          cid:'mybill',
          aid:'open'
      });
      this.goLink('/bill');
    }
  }
};
</script>

